/* Contenedor del 铆cono de usuario */
.user-icon-container {
    position: relative;
    display: flex;
    align-items: center; /* Alinea verticalmente con otros elementos del navbar */
}

/* cono de usuario */
.user-icon {
    font-size: 1.2rem; /* Tama帽o del 铆cono */
     cursor: pointer;
    padding: 0.5rem; /* Espaciado interno */
    transition: color 0.3s ease; /* Transici贸n suave para el hover */
    margin-bottom: 7px;
}

.user-icon:hover {
    color: #92b3d6; /* Cambia el color al hacer hover */
}

/* Men煤 desplegable */
.user-dropdown {
    position: absolute;
    top: 100%; /* Posiciona el men煤 debajo del 铆cono */
    right: 0; /* Alinea el men煤 a la derecha */
    background: white;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Sombra suave */
    min-width: 1500px; /* Ancho m铆nimo del men煤 */
    z-index: 1000; /* Asegura que el men煤 est茅 por encima de otros elementos */
}

/* Elementos del men煤 desplegable */
.user-dropdown .dropdown-item {
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
    color: #333;
    text-decoration: none;
    display: block;
    transition: background 0.3s ease; /* Transici贸n suave para el hover */
}

.user-dropdown .dropdown-item:hover {
    background: #f8f9fa; /* Fondo al hacer hover */
}

/* Divisor del men煤 */
.user-dropdown .dropdown-divider {
    border-top: 1px solid #ddd;
    margin: 0.5rem 0;
}

/* Estilos responsivos para dispositivos m贸viles */
@media (max-width: 768px) {
    .user-icon {
        font-size: 1.2rem; /* Tama帽o m谩s peque帽o en m贸viles */
    }

    .user-dropdown {
        min-width: 120px; /* Ancho m谩s peque帽o en m贸viles */
    }

    .user-dropdown .dropdown-item {
        padding: 0.4rem 0.8rem;
        font-size: 0.8rem;
    }
}